import { request, requestDownload } from 'utils';

const prefix = '/api/finance';

export const getStudentReduction = dataSearch =>
  request({
    prefix,
    url: `/StudentReduction/BrowseStudentReduction`,
    params: { ...dataSearch },
    method: 'GET',
  });

export const getFeeObject = () =>
  request({
    prefix,
    url: `/FeeObject`,
    method: 'GET',
  });

export const getPolicyObject = () =>
  request({
    prefix: '/api/categories',
    url: `/PolicyObject`,
    method: 'GET',
  });
export const getReceivable = () =>
  request({
    prefix,
    url: `/Receivable`,
    method: 'GET',
  });

export const getStudentReductionBySemester = dataSearch => {
  const paramsSearch = {
    receivableId: dataSearch.receivableId,
    semesterId: dataSearch.semesterId,
    yearEduId: dataSearch.yearEduId,
  };
  return request({
    prefix,
    url: `/StudentReduction/BrowseStudentReductionBySemester`,
    params: paramsSearch,
    method: 'GET',
  });
};

export const addStudentReduction = listNewStudent =>
  request({
    prefix,
    url: `/StudentReduction`,
    data: listNewStudent,
    method: 'POST',
  });
export const deleteStudentReduction = listId =>
  request({
    prefix,
    url: `/StudentReduction`,
    data: listId,
    method: 'DELETE',
  });

export const downloadFileStudentReduction = dataUpdate =>
  requestDownload({
    defaultFileName: 'FileStudentReduction.xlsx',
    prefix,
    url: `/StudentReduction/ExportStudentReduction`,
    data: dataUpdate,
    method: 'POST',
  });



