import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax, YearSemesterInput } from 'components/common';
import { getPolicyObject, getFeeObject } from 'services/StudentReduction';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
// import BtnCopy from './BtnCopy';
import './style.scss';

@Form.create({ name: 'FormSearchClasses' })
class FormSearchStudent extends Component {
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.dataForm(values);
      updateQueryParams(values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn lớp!',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn năm học!',
          },
        ],
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate optionFieldDecorator={optionFieldDecorator} form={this.props.form} />
        </Row>
        <Row gutter={24}>
          <YearSemesterInput
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            span={5}
          />
          <Col span={5}>
            <Form.Item label="ĐTCS">
              {getFieldDecorator('policyObjectId', {})(<SelectAjax service={getPolicyObject} />)}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="ĐTHP">
              {getFieldDecorator('fOId', {})(<SelectAjax service={getFeeObject} />)}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Họ tên">{getFieldDecorator('fullname')(<Input />)}</Form.Item>
          </Col>
        </Row>
        <Row gutter={24} type="flex" justify="space-between">
          <Col xs={24} sm={24} md={12} lg={4} xl={4} order={2}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
          {/* <Col xs={24} sm={24} md={12} lg={4} xl={4} order={1}>
            <Popconfirm
              title="Bạn muốn sao chép sang kỳ sau?"
              onConfirm={this.handleCopyToNextSemedter}
              okText="Đúng"
              cancelText="Hủy"
            >
              <BtnCopy title="Sao chép sang kỳ sau" style={{ margin: '5px 0' }} />
            </Popconfirm>
          </Col> */}
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



